<template>
    <el-container id="main" style="height: 100%;">
        <el-container class="login-container">
            <div id="LoginTable">
                <div class="div-out">
                    <div class="div-inner">
                        <p class="login-p1">知他</p>
                        <p class="login-p2">共享智能平台</p>
                        <p class="login-p3">科技部重点研发计划资助项目（2018YFB1403000）</p>
                        <div class="login-input">
                            <div>
                                <img src="static/images/login_user.png" width="23px" height="28px" alt="">
                            </div>
                            <input v-model="username" class="username" placeholder="请输入您的登录账号"></input>
                        </div>
                        <div class="login-input" style="margin-top: 25px;">
                            <div>
                                <img src="static/images/login_pwd.png" alt="" width="25px" height="27px">
                            </div>
                            <input v-model="password" type="password"  class="password" placeholder="请输入您的登录密码"></input>
                        </div>
                        <div class="login-btn-div">
                            <el-button type="primary" class="login-btn" @click="toLogin" @keyup.enter.native="toLogin()">登录</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </el-container>
    </el-container>
</template>
<style>
    @import "../../static/css/login.css";
</style>
<script>
import $ from "jquery";

export default {
    name: "login",
    data: function() {
        return {
            username:'',
            password:'',
        }
    },
    created(){
        var _self = this;
        document.onkeydown = function(e){
            if(window.event == undefined){
                var key = e.keyCode;
            }else{
                var key = window.event.keyCode;
            }
            if(key == 13){
                _self.toLogin();
            }
        }
    },
    methods: {
        // 登录
        toLogin:function(){
            var that=this
            if(that.username==""){
                this.$message.error('请输入用户名');
            }
            else if(that.password==""){
                this.$message.error('请输入密码');
            }
            else{
                that.axios.post('/userLogin',this.qs.stringify({
                    'operator':that.username,
                    'password':that.password
                }),{
                    headers:{
                        'Content-Type':'application/x-www-form-urlencoded',
                    }
                },{
                    emulateJSON:true
                }).then(function(response){
                    if(response.data.message.state){
                        document.onkeydown = undefined;
                        var identity=response.data.userType;
                        that.$message({
                            message: '登录成功',
                            type: 'success'
                        });
                        Cookies.set("fedname",that.username)
                        Cookies.set("fedrole",identity)
                        Cookies.set("fedtoken",response.data.token)

                        if(identity=="平台管理员"){ //平台管理员
                            setTimeout(function(){
                                that.$router.push('/nodeManage')
                            },2000);
                        }
                        else if(identity=="节点管理员"){ //节点管理员
                            setTimeout(function(){
                                that.$router.push('/nodeSet')
                            },2000);
                        }
                        else if(identity=="节点普通用户"){ //节点普通用户
                            setTimeout(function(){
                                that.$router.push('/dataManage')
                            },2000);
                        }
                    }
                    else {
                        that.$message.error(response.data.message);
                    }
                },function(error){
                    that.$message.error(error.response.data.message);
                })
            }
        },
    },
};
</script>




